import React, {useRef} from 'react';

import { useHistory } from "react-router-dom";

import { CSSReset, Text, Box, Flex, Image, Link, Grid } from "@chakra-ui/core";

import NavigationBar from '../components/NavigationBar.js';
import Contact from '../containers/Contact.js';
import { Title, HeaderBig, TextMedium } from '../components/Texts.js';

import grad from '../media/grad.png';
import { TagBoxes } from '../components/Layouts.js';

function Home() {
  const history = useHistory()

  const ref = {
    "Contact": useRef(null)
  }

  const navClick = (compName) => {
    if(compName === 'Contact'){
      window.scrollTo(0, ref[compName].current.offsetTop - 64) 
    }
    else{
      history.push("/home");
    }
  }

  const Block = (props) => {
    return (
      <Flex direction='column' mt='20px' px='20px' w='100%' maxW='1200px' {...props}>
        {props.children}
      </Flex>
    )
  }

  return (
    <Box bg='gray.700' color='white' bgImage={"url(" + grad + ")"} bgRepeat="no-repeat" bgSize='100% 100%'>
      <CSSReset />
      <NavigationBar navClick={navClick} names={['Profile', 'Education', 'Skills', 'Projects', 'Experiences', 'Honors', 'Contact']}/>
      <Flex w='100%' pb='3rem' pt='1rem' direction='column' bg='trans.gray' alignItems='center'>
        <Title>Generative Facial Cosmetics</Title>
        <Block>
          <HeaderBig>
            People Involved
          </HeaderBig>
          <TextMedium>
            <span style={{color: '#63B3ED', fontWeight:'bold'}}>Myself</span> as a <span style={{fontWeight:'bold'}}>Developer</span>
          </TextMedium>
        </Block>
        <Block>
          <HeaderBig>
            Technologies
          </HeaderBig>
          <TagBoxes keywords={['Tensorflow', 'Keras', 'Machine Learning', 'Generative Adversarial Networks', 'Computer Vision']}/>
        </Block>
        <Block>
          <HeaderBig>
            Objectives
          </HeaderBig>
          <TextMedium>
            Hello
          </TextMedium>
        </Block>
      </Flex>
      <hr/>
      <div ref={ref['Contact']}></div>
      <Contact/>
    </Box>
  );
}

export default Home;
